/* App.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
}

.header {
  background-color: #2c3e50;
  color: white;
  padding: 3rem 1rem;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.subtitle {
  font-size: 1.2rem;
  margin-top: 0.5rem;
  opacity: 0.9;
}

.content-container {
  flex: 1;
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.posts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  padding: 1rem;
}

.post-card {
  background: white;
  border-radius: 10px;
  padding: 1.5rem;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  display: flex;
  flex-direction: column;
}

.post-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.post-meta {
  margin-bottom: 1rem;
}

.post-date {
  font-size: 0.9rem;
  color: #7f8c8d;
}

.post-title {
  color: #2c3e50;
  margin: 0.5rem 0;
  font-size: 1.5rem;
}

.post-excerpt {
  color: #34495e;
  line-height: 1.6;
  margin-bottom: auto;
}

.read-more {
  align-self: flex-start;
  background: none;
  border: none;
  color: #3498db;
  cursor: pointer;
  padding: 0.5rem 0;
  margin-top: 1rem;
  font-weight: bold;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 2rem;
  border-radius: 10px;
  max-width: 800px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
}

.close-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #2c3e50;
}

.post-full-content {
  white-space: pre-wrap;
  line-height: 1.6;
  font-family: inherit;
  margin-top: 1rem;
}

.footer {
  background: #2c3e50;
  color: white;
  text-align: center;
  padding: 1rem;
  margin-top: auto;
}